import React from 'react';
import Modal from 'react-modal';
import merge from 'lodash/merge'
import { IoIosClose } from 'react-icons/io';

import Box from './Box'
import Button from './Button';
import theme, { responsive } from './ThemeProvider/theme'

const defaultStyles = {
  overlay: {
    zIndex: theme.zOrder[3],
    backgroundColor: 'rgba(0,0,0,0.7)'
  },
  content: {
    width: '90%',
    maxWidth: '40em',
    top: '50%',
    left: '50%',
    right: 'auto',
    bottom: 'auto',
    marginRight: '-50%',
    transform: 'translate(-50%, -50%)',
    borderRadius: '1em',
    border: '5px solid black',
    padding: '0',
    backgroundColor: 'white',
    overflow: 'unset',
  }
};

export default ({
  children,
  border,
  borderRadius,
  customStyles,
  py,
  closeIcon,
  onRequestClose,
  ...props
}) => (
  <Modal
    style={merge({}, defaultStyles, { content: customStyles })}
    onRequestClose={onRequestClose}
    {...props}
  >
    {closeIcon &&  (
      <Box textAlign="right" pr="1em" pt="1em">
        <Button.Transparent display="inline-block" lineHeight="0" px="0" py="0" onClick={onRequestClose}>
          <IoIosClose size="2.5em" />
        </Button.Transparent>
      </Box>
    )}
    <Box pb="1em">
      {children}
    </Box>
  </Modal>
)
